"use client";
import { useEffect, useState } from "react";
import { fetchOrderbook } from "../lib/api";
import { Line } from "react-chartjs-2";
import { Chart as ChartJS, registerables } from "chart.js";

ChartJS.register(...registerables);

const Orderbook = () => {
  const [orderbook, setOrderbook] = useState({ bids: [], asks: [] });
  const [spread, setSpread] = useState(0);
  const [imbalance, setImbalance] = useState(0);

  useEffect(() => {
    const interval = setInterval(async () => {
      const data = await fetchOrderbook();
      setOrderbook(data);
      
      // Calculate spread
      const spreadValue =
        data.bids[0] && data.asks[0]
          ? parseFloat(data.asks[0][0]) - parseFloat(data.bids[0][0])
          : 0;
      setSpread(spreadValue);

      // Calculate orderbook imbalance
      const bidVolume = data.bids.reduce((sum, bid) => sum + parseFloat(bid[1]), 0);
      const askVolume = data.asks.reduce((sum, ask) => sum + parseFloat(ask[1]), 0);
      setImbalance(bidVolume / (bidVolume + askVolume));
    }, 1000); // Update every second

    return () => clearInterval(interval);
  }, []);

  const marketDepthData = {
    labels: orderbook.bids.map((bid) => bid[0]),
    datasets: [
      {
        label: "Bids",
        data: orderbook.bids.map((bid) => bid[1]),
        borderColor: "green",
        fill: false,
      },
      {
        label: "Asks",
        data: orderbook.asks.map((ask) => ask[1]),
        borderColor: "red",
        fill: false,
      },
    ],
  };

  return (
    <div>
      <h2>Real-time BTC-USD Orderbook</h2>
      <div>
        <h3>Spread: {spread}</h3>
        <h3>Imbalance: {imbalance}</h3>
      </div>
      <div>
        <h3>Bids</h3>
        <ul>
          {orderbook.bids.map((bid, index) => (
            <li key={index}>{`Price: ${bid[0]}, Quantity: ${bid[1]}`}</li>
          ))}
        </ul>
      </div>
      <div>
        <h3>Asks</h3>
        <ul>
          {orderbook.asks.map((ask, index) => (
            <li key={index}>{`Price: ${ask[0]}, Quantity: ${ask[1]}`}</li>
          ))}
        </ul>
      </div>
      <div>
        <h3>Market Depth Chart</h3>
        <Line data={marketDepthData} />
      </div>
    </div>
  );
};

export default Orderbook;
